/* app/globals.css - minimal, responsive styles */

:root{
  --max-width: 900px;
  --gap: 16px;
  --muted: #6b7280;
  --bg: #f8fafc;
  --card: #ffffff;
  font-family: Inter, system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial;
}

html,body,#__next {
  height: 100%;
}

body {
  margin: 0;
  background: var(--bg);
  color: #111827;
  line-height: 1.4;
}

.container {
  max-width: var(--max-width);
  margin: 32px auto;
  padding: 0 16px;
}

.header {
  display:flex;
  justify-content:space-between;
  align-items:center;
  gap:12px;
  margin-bottom: 18px;
}

.card {
  background: var(--card);
  border-radius: 8px;
  padding: 16px;
  box-shadow: 0 1px 3px rgba(15,23,42,0.05);
}

/* Table / list */
.user-list {
  width: 100%;
  border-collapse: collapse;
}

.user-row {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 12px;
  align-items: center;
  padding: 12px 8px;
  border-bottom: 1px solid #eef2f7;
}

.user-row:last-child { border-bottom: none; }

.user-name { font-weight: 600; }
.user-email { color: var(--muted); font-size: 0.95rem; }

/* Responsive: stack columns on small screens */
@media (max-width: 640px) {
  .user-row {
    grid-template-columns: 1fr;
  }
}

/* Search input */
.input {
  width: 100%;
  padding: 10px;
  border-radius: 6px;
  border: 1px solid #e6edf3;
  outline: none;
  font-size: 1rem;
}

/* Pagination */
.pager {
  display:flex;
  gap:8px;
  align-items:center;
  margin-top: 12px;
}
.btn {
  padding: 8px 12px;
  background: none;
  border: 1px solid #e6edf3;
  border-radius: 6px;
  cursor:pointer;
}
.btn[disabled] {
  opacity: 0.5;
  cursor: not-allowed;
}
.small-muted {
  color: var(--muted);
  font-size: 0.9rem;
}
.spinner {
  display:inline-block;
  width:20px; height:20px;
  border:3px solid #e6edf3;
  border-top-color:#3b82f6;
  border-radius:50%;
  animation:spin 1s linear infinite;
}
@keyframes spin { to { transform: rotate(360deg); } }
